import distortionArgsMixin from './distortion-args-mixin';

export default {
  name: 'ArcArgs',

  mixins: [distortionArgsMixin],

  computed: {
    fields() {
      return [
        {
          label: 'Arc Angle',
          clearable: false,
          min: 0.1,
          max: 360
        },
        {
          label: 'Rotation Angle',
          clearable: true
        },
        {
          label: 'Outer Radius',
          clearable: true
        },
        {
          label: 'Inner Radius',
          clearable: true,
          min: 0
        }
      ];
    }
  },

  methods: {
    emitInput() {
      let index;
      let value = this.getFilteredArray();

      for (let i = value.length - 1; i > 0; i--) {
        if (value[i] === null) {
          index = i;
        } else {
          break;
        }
      }

      if (index) {
        value.splice(index);
      }

      this.$emit('input', value);
    }
  }
}
