import {massSimpleProxy} from '../../../../utils';

import {filter} from '@alxcube/lens';

const props = {
  name: String,

  cubicBC_b: Number,

  cubicBC_c: Number,

  label: String,

  disabled: Boolean
};

export default {
  name: 'FilterFunctionConfigurator',

  props,

  computed: {
    ...massSimpleProxy(Object.keys(props), 'proxy', ['label', 'disabled']),

    filterFunctionNames() {
      return Object.keys(filter.filterFunctions);
    }
  }
}
