import ColorInput from './color-input';
import FileSelectionArea from '../../file-selection-area/index';

import { interpolation, vpx } from '@alxcube/lens';

function makeItems(object) {
  return Object.keys(object).map(text => ({
    text,
    value: object[text]
  }));
}

export default {
  name: 'ImageSettings',

  components: {
    ColorInput,
    FileSelectionArea
  },

  props: {
    interpolationMethod: {
      type: Number,
      default: interpolation.AVERAGE
    },

    virtualPixelMethod: {
      type: Number,
      default: vpx.TRANSPARENT
    },

    backgroundColor: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      }
    },

    viewportOffset: {
      type: Object,
      default() {
        return {x: 0, y: 0}
      }
    },

    disabled: Boolean
  },

  data() {
    return {
      localViewportOffset: {x: 0, y: 0}
    };
  },

  computed: {
    selectedInterpolationMethod: {
      get() {
        return this.interpolationMethod;
      },

      set(method) {
        this.$emit('update:interpolation-method', method);
      }
    },

    selectedVirtualPixelMethod: {
      get() {
        return this.virtualPixelMethod;
      },

      set(method) {
        this.$emit('update:virtual-pixel-method', method);
      }
    },

    selectedBackgroundColor: {
      get() {
        return this.backgroundColor;
      },

      set(color) {
        return this.$emit('update:background-color', color);
      }
    },

    interpolationMethods() {
      return makeItems(interpolation);
    },

    virtualPixelMethods() {
      return makeItems(vpx);
    }
  },

  watch: {
    viewportOffset: {
      handler({x, y}) {
        if (x !== this.localViewportOffset.x || y !== this.localViewportOffset.y) {
          this.localViewportOffset = {x, y};
        }
      },

      immediate: true
    },

    localViewportOffset: {
      handler({x, y}) {
        if (x !== this.viewportOffset.x || y !== this.viewportOffset.y) {
          this.$emit('update:viewport-offset', {x, y});
        }
      },

      deep: true
    }
  }
}
