import CoordsPair from './coords-pair';

function createComputed(prop) {
  return {
    get() {
      return this[prop];
    },

    set(value) {
      this.$emit(`update:${prop}`, value);
    }
  };
}

export default {
  name: 'PointsMapping',

  components: {
    CoordsPair
  },

  props: {
    u: {
      type: Number,
      default: 0
    },

    v: {
      type: Number,
      default: 0
    },

    x: {
      type: Number,
      default: 0
    },

    y: {
      type: Number,
      default: 0
    },

    index: Number,

    disabled: Boolean,

    enableDeletion: {
      type: Boolean,
      default: true
    },

    activeOffset: {
      type: [Number, null],
      default: null
    }
  },

  data() {
    return {
      showMenu: false
    };
  },

  computed: {
    uval: createComputed('u'),
    vval: createComputed('v'),
    xval: createComputed('x'),
    yval: createComputed('y'),

    labelIndex() {
      return this.index !== undefined ? this.index : '';
    },

    ulabel() {
      return `u${this.labelIndex}`;
    },

    vlabel() {
      return `v${this.labelIndex}`;
    },

    xlabel() {
      return `x${this.labelIndex}`;
    },

    ylabel() {
      return `y${this.labelIndex}`;
    }
  },

  methods: {
    pickApex(location, offset) {
      this.$emit('pick-source-apex', {location, offset});
      this.showMenu = false;
    },

    pickPoint(offset) {
      this.$emit('pick-point', offset);
      this.showMenu = false;
    }
  }
}
