export default {
  name: 'CoordsPair',

  props: {
    x: {
      type: Number,
      default: 0
    },

    y: {
      type: Number,
      default: 0
    },

    xLabel: {
      type: String,
      default: 'x'
    },

    yLabel: {
      type: String,
      default: 'y'
    },

    disabled: Boolean
  },

  computed: {
    xval: {
      get() {
        return this.x;
      },

      set(x) {
        this.$emit('update:x', x);
      }
    },

    yval: {
      get() {
        return this.y;
      },

      set(y) {
        this.$emit('update:y', y);
      }
    }
  }
}
